.transition {
  transition-property: background-color, border-color, color, fill, stroke, opacity, box-shadow, transform;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.transition-opacity {
  transition-property: opacity;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 150ms;
}

.ease-linear {
  transition-timing-function: linear;
}

.ease-in-out {
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

.duration-75 {
  transition-duration: 75ms;
}

.duration-500 {
  transition-duration: 500ms;
}

.duration-700 {
  transition-duration: 700ms;
}

.delay-1000 {
  transition-delay: 1000ms;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

@keyframes ping {
  75%, 100% {
    transform: scale(2);
    opacity: 0;
  }
}

@keyframes pulse {
  50% {
    opacity: .5;
  }
}

@keyframes bounce {
  0%, 100% {
    transform: translateY(-25%);
    animation-timing-function: cubic-bezier(0.8,0,1,1);
  }

  50% {
    transform: none;
    animation-timing-function: cubic-bezier(0,0,0.2,1);
  }
}

body{
  margin:0;
  font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing:antialiased;
  -moz-osx-font-smoothing:grayscale
}

code{
  font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace
}

body:not(.user-is-tabbing) button:focus,body:not(.user-is-tabbing) input:focus,body:not(.user-is-tabbing) select:focus,body:not(.user-is-tabbing) textarea:focus,body:not(.user-is-tabbing) a:focus{
  outline:none
}

body.user-is-tabbing button:focus,body.user-is-tabbing input:focus,body.user-is-tabbing select:focus,body.user-is-tabbing textarea:focus,body.user-is-tabbing a:focus{
  outline-color:purple
}

html{
  box-sizing:border-box
}

*,*:before,*:after{
  box-sizing:inherit
}

body::-webkit-scrollbar-track{
  -webkit-box-shadow:inset 0 0 6px rgba(110,110,110,0.1);
  background-color:#414141;
  border-radius:10px
}

body::-webkit-scrollbar{
  width:10px;
  background-color:#414141
}

body::-webkit-scrollbar-thumb{
  border-radius:10px;
  -webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
  background-color:#6f7174
}

.blog-mainContent::-webkit-scrollbar-track{
  -webkit-box-shadow:inset 0 0 6px rgba(110,110,110,0.1);
  background-color:#6f7174;
  border-radius:10px
}

.blog-mainContent::-webkit-scrollbar{
  width:10px;
  background-color:#6f7174
}

.blog-mainContent::-webkit-scrollbar-thumb{
  border-radius:10px;
  -webkit-box-shadow:inset 0 0 6px rgba(0,0,0,0.3);
  background-color:#aaa6a6
}

.blog-body img{
  display:none
}

.vertical-divider{
  height:65px;
  width:2px;
  z-index:10;
  margin:0 1rem;
  background-image:linear-gradient(to bottom, rgba(0,0,0,0) 0, rgba(100,100,100,0.65) 50%, rgba(0,0,0,0) 100%)
}

.horizontal-divider{
  height:2px;
  width:100%;
  z-index:10;
  background-image:linear-gradient(to left, rgba(0,0,0,0) 0, rgba(100,100,100,0.65) 50%, rgba(0,0,0,0) 100%)
}

h1{
  font-size:2em
}

h2{
  font-size:1.5em
}

h2{
  font-size:1.17em
}

.loader{
  border-top-color:#bba0eb;
  animation:spinner 1.5s linear infinite
}

@keyframes spinner{
  0%{
    transform:rotate(0deg)
  }

  100%{
    transform:rotate(360deg)
  }
}

